<template>
  <div class="invite">
    <img src="@/assets/images/my/bg1.png" alt="" style="width:80%;margin:30px auto;">
    <div class="code">
      <h1>我是{{$route.query.name}}</h1>
      <h1>我为您推荐公众号</h1>
      <img class="codeImg" :src="img"/>
      <p>长按识别二维码</p>
      <p>关注公众号</p>
    </div>
    <!-- <div class="bot">
      <div class="left flex">
        <img src="@/assets/images/my/invitation_dload@2x.png" />
        <p>长按二维码保存到相册</p>
      </div>
      <div class="right flex">
        <img src="@/assets/images/my/invitation_share@2x.png" />
        <p>分享给好友</p>
      </div>
    </div> -->
  </div>
</template>


<script>
import { yaoqingma } from "@/axios/getData";
export default {
  data() {
    return {
      img: ''
    };
  },
  created() {
    this.getData();
  },
  methods: {
    async getData() {
      var res = await yaoqingma();
      this.img = res.data.ewm
    }
  }
};
</script>


<style scoped lang="scss">
.invite {
  padding:1px 0;
  background: -webkit-linear-gradient(bottom,#011957,#010d32);
  height: 100vh;
  .code {
    margin: 0 100px;
    background: rgba(255, 255, 255, 1);
    border-radius: 13px;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 20px 0;
    margin-bottom:80px;
    h1 {
      font-size: 18px;
    }
    .codeImg {
      height: 300px;
      width: 300px;
    }
  }
  .bot {
    display: flex;
    justify-content: space-around;
    color: #fff;
    margin-top: 30px;
    .flex {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    img {
      width: 30px;
      height: 30px;
      margin-bottom: 10px;
    }
  }
}
</style>



